import React,{useState} from 'react';
import defaultImg from './default.png';
import './index.less';

function Part3 (props) {
    const [data,setData] = useState(props.data || {
        imgUrl:'',
        list:[
            {name:'姓名1',place:'湖南',date:'1988.07',major:'材料与工程专业',sutdentNo:'001'},
            {name:'姓名2',place:'湖南',date:'1999.12',major:'材料与工程专业',sutdentNo:'002'},
            {name:'姓名3',place:'湖北',date:'1988.03',major:'材料与工程专业',sutdentNo:'003'},
            {name:'姓名4',place:'山东',date:'1988.11',major:'材料与工程专业',sutdentNo:'004'},
            {name:'姓名5',place:'河南',date:'1988.09',major:'材料与工程专业',sutdentNo:'005'},
            {name:'姓名6',place:'广西',date:'1999.03',major:'高分子化学',sutdentNo:'006'},
        ]
    })
    let onerror = () =>{
        setData({
            ...data,
            imgUrl:defaultImg
        })
    }
    return (
        <div className="item part3">
            <div className='img'>
                <img alt='dormitory' src={data.imgUrl} onError={onerror}/>
            </div>
            <div className='list'>
                {
                    data.list.map(sutdent=>{
                        return <div className="student-item" key={sutdent.sutdentNo}>
                            <span className='name'>{sutdent.name}</span>
                            <span className='place'>{sutdent.place}</span>
                            <span className='date'>{sutdent.date}</span>
                            <span className='major'>{sutdent.major}</span>
                        </div>
                    })
                }
                <div></div>
            </div>
        </div>
    )
}

export default Part3