import React,{useEffect} from 'react';
import echarts from 'echarts';

function Chart({data,...props}){
    useEffect(() => {
        let myChart = echarts.init(document.getElementById('part7'));
        let chartData = data || [];
        var option = {
            color:['#5B8FF9','#5AD8A6','#5D7092','#F6BD16','#E8684A','#6DC8EC','#A56FFF'],
            grid:{
                top:20,
                bottom:"50px",
                left:40,
                right:40
            },
            legend:{
                icon:'circle',
                textStyle:{
                    color:'fff'
                }
            },
            series:[
                {
                    type: 'pie',
                    data:chartData,
                    radius:['30%','55%'],
                    label:{
                        formatter:'{b}\n {c}({d}%)'
                    },
                    itemStyle:{
                        borderWidth:4,
                        borderColor:'#6236FF'
                    }
                }
            ]
        }
        myChart.setOption(option); 
    }, [data])

    return (
        <div className='chart'>
            <div className='chart-content' id='part7'></div>
        </div>
    )
}

export default Chart;