import React,{useEffect} from 'react';
import echarts from 'echarts';

function Chart({data,...props}){
    useEffect(() => {
        let myChart = echarts.init(document.getElementById('part4'));
        let chartData = data || [];
        var option = {
            xAxis: {
                type: 'category',
                data: chartData.map(item=>item.college),
                axisLabel:{
                    rotate:20 //label文字旋转角度
                },
                axisLine:{
                    show:false
                },
                axisTick:{
                    show:false
                }
            },
            yAxis: {
                type: 'value',
                splitLine:{
                    show:false
                },
                axisLine:{
                    show:false
                },
                axisTick:{
                    show:false
                }
            },
            grid:{
                top:20,
                bottom:50,
                left:40,
                right:40
            },
            series:[
                {
                    type: 'bar',
                    data:chartData.map(item=>item.value),
                    barCategoryGap:"40%",//同一系列间距
                    itemStyle:{
                        color:"#F89E3C",//柱子颜色
                    },
                }
            ]
        }
        myChart.setOption(option); 
    }, [data])

    return (
        <div className='chart'>
            <div className='chart-content' id='part4'></div>
        </div>
    )
}

export default Chart;