import React,{useEffect} from 'react';
import echarts from 'echarts';

function Chart({data,...props}){
    useEffect(() => {
        let myChart = echarts.init(document.getElementById('part8-1'));
        let chartData = data || [];
        var option = {
            title:{
                text:'男生最多的专业排名',
                backgroundColor:'rgba(29,188,251,0.42)',
                textStyle:{
                    color:"#00EBFF", 
                },
                right:10,
            },
            xAxis: {
                type: 'category',
                data: chartData.map(item=>item.name),
                axisLabel:{
                    // rotate:20 //label文字旋转角度
                    color:'#fff'
                },
                axisLine:{
                    // show:false
                    lineStyle:{
                        color:'#fff'
                    }
                },
                axisTick:{
                    show:false
                }
            },
            yAxis: {
                type: 'value',
                axisLabel:{
                    // rotate:20 //label文字旋转角度
                    color:'#fff'
                },
                splitLine:{
                    show:false
                },
                axisLine:{
                    show:false
                },
                axisTick:{
                    show:false
                }
            },
            grid:{
                top:20,
                bottom:"50px",
                left:40,
                right:40
            },
            series:[
                {
                    type: 'bar',
                    data:chartData.map(item=>item.boy),
                    barCategoryGap:"40%",//同一系列间距
                    itemStyle:{
                        color:"#00EBFF",//柱子颜色
                    },
                },{
                    type:'bar',
                    data:chartData.map(item=>item.girl),
                    itemStyle:{
                        color:'#F7B500'
                    }
                }
            ]
        }
        myChart.setOption(option); 
    }, [data])

    return (
        <div className='chart'>
            <div className='chart-content' id='part8-1'></div>
        </div>
    )
}

export default Chart;