import React from 'react';
import './index.less'
import VS from './vs.png';
import Boy from './boy.png';
import Girl from './girl.png';
import Chart1 from './chart1';
import Chart2 from './chart2';

function Part8 (props) {
    let data = props.data || {
        allScholl:{//全校男女比例
            boy:'62.00%',
            girl:'38.00%'
        },
        boyRange:[//男生最多专业排名，取前三
            {name:'土木工程',boy:55,girl:9},
            {name:'电子信息',boy:44,girl:12},
            {name:'机械工程',boy:33,girl:11},
        ],
        girlRange:[//女生最多专业排名，取前三
            {name:'电子商务',boy:7,girl:48},
            {name:'经济管理',boy:10,girl:45},
            {name:'外语',boy:12,girl:38},
        ]
    }
    return (
        <div className="item part8">
            <div className="textContent">
                <p>你并不孤单，</p>
                <p>对面的男孩/女孩看过来了！</p>
            </div>
            <div className="chartTitle">全校男女比例</div>
            <div className='all'>
                <img className='pic' src={Boy} alt='boy'></img>
                <div className='allCenter'>
                    <img src={VS} alt='vs'/>
                    <div className='circleContainer'><span className='boyCircle'></span>男 <span className='girlCircle'></span>女</div>
                    <div className='numContainer'>
                        <span className='boyNum'>{data.allScholl.boy}</span>
                        <span className='girlNum'>{data.allScholl.girl}</span>
                    </div>
                </div>
                <img className='pic' src={Girl} alt='girl'></img>
            </div>
            <div className='chartContainer'>
                <div className='containerTitle'>专业男女比例排名</div>
                <Chart1 data={data.boyRange}/>
                <Chart2 data={data.girlRange}/>
            </div>
        </div>
    )
}

export default Part8